body{
    background-color: #111;
}

.column-3 {
    width: 75%;
    float: left;
}
.column-2 {
    width: 50%;
    float: left;
}
.column-1 {
    width: 25%;
    float: left;
}
button.calc-button {
    width: 100%;
    height: 90px;
    border: 2px solid #333;
    background-image: linear-gradient(to right bottom, #3b3b3b 0%, black 100%);
    font-size: 36px;
    color: #555;
}
input.result {
    width: 100vw;
    height: 100px;
    border: none;
    text-align: right;
    position: relative;
    max-width: 100vw;
    margin-right: 50px;
    float: right;
    font-size: 50px;
    color: #888;
    background-color: #111;
}
.floaty-last{
    position: absolute;
    right: 50px;
    z-index: 99;
    color: #555;
}